// @flow
const spinnerImgSrc = `data:image/svg+xml;base64,`;

export const displayBlackLoadingScreen = (externalWindow: WindowProxy) => {
  externalWindow.document.write(
    `<html>
  <head>
    <style>
      body {
        margin: 0;
        padding: 0;
        background-color: #000000;
        overflow: hidden;
        height: 100vh;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
      }
      @keyframes spin {
        100% {
          transform:rotate(360deg);
        }
      }
      .spinning-loader {
        animation: spin 3s linear infinite;
      }
    </style>
  </head>
  <body>
      <img class="spinning-loader" width="50" alt="Loading..." src="${spinnerImgSrc}">
  </body>
</html>`
  );
};
